    getInvoiceDatas() {
      getInvoiceDatas()
        .then((res) => {
          this.InvoiceTableData = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleEditInvoice(index, row) {
      this.$router.push({
        path: "Projectform",
        query: { project_code: row.project_code, activeName: "five" },
      });
    },
  },
};
